@mixin smakk-base {
    //Typography
    html,
    body {
        font-size: 18px;
        @include primary-font-bold;
        color: #fff;
        background: $c-grey;
    }
    h1,
    h2,
    h3,
    h4,
    .h1,
    .h2,
    .h3,
    .h4 {
        margin: 0;
        margin-bottom: 20px;
        @include smakk-breakpoint(large) {
            margin-bottom: 40px;
        }
    }
    h1,
    .h1 {
        @include primary-font-extrabold;
        @include smakk-fluid-type(37.5em, 80em, $h1-min, $h1-max);
        letter-spacing: -2px;
        line-height: 1;
    }
    h2,
    .h2,
    h1.smaller {
        @include primary-font-extrabold;
        @include smakk-fluid-type(37.5em, 80em, $h2-min, $h2-max);
        letter-spacing: -1px;
        line-height: 1.2
    }
    h3,
    .h3,
    h2.smaller {
        @include primary-font-bold;
        @include smakk-fluid-type(37.5em, 80em, $h3-min, $h3-max);
        letter-spacing: -1px;
        line-height: 1.3
    }
    h2.smaller {
        @include primary-font-extrabold;
    }
    h4,
    .h4,
    h3.smaller {
        @include primary-font-bold;
        @include smakk-fluid-type(37.5em, 80em, $h4-min, $h4-max);
        margin-bottom: 20px;
        line-height: 1.2
    }
    p {
        @include primary-font-bold;
        font-size: 1em;
        line-height: 24px;
    }
    blockquote {
        position: relative;
        @include primary-font-semibold;
        margin: $scale-2 0;
        max-width: 500px;
        cite {
            margin-top: $scale-5;
            font-style: normal;
            display: block
        }
        &:after {
            content: '';
            position: absolute;
            height: 25px;
            width: 95px;
            bottom: 55px;
            background-image: url(svg/quotation-mark.svg);
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
    a,
    a:link,a:visited
    {
        border-bottom: 2px solid;
        text-decoration: none !important;
        color: inherit;
        .is-blue & {
            border-bottom: 2px solid $c-green;
            &:hover {
                border-bottom: 2px solid $c-blue;
            }
        }
        .is-bg-green & {
            &:hover {
                border-bottom: 2px solid $c-blue;
            }
        }
    }
    a:hover {
        color: inherit;
        border-bottom: 2px solid $c-green;
    }
  a.button {
   border: 1px transparent;
    &:hover {
      border: 1px transparent
    }
  }
  a.h1, a.h2, a.h3, a.h4 {
    border-bottom: 0;
    &:hover {
       border-bottom: 0 !important;
    }
  }


    //Forms , inputs buttons
    label {
        color: $c-green;
        @include primary-font-semibold;
        display: block
    }
    input:not([type="submit"]),
    select,
    textarea {
        width: 100%;
        border: 0;
        border-bottom: 2px solid $c-blue;
        border-radius: 0;
        margin-top: $scale-5;
        margin-bottom: $scale-2;
        color: $c-green;
        @include primary-font-semibold;
        padding: 2px;
        background: rgba(0, 0, 0, 0);
        &::placeholder,
        option,
        label {
            color: $c-green;
            @include primary-font-semibold
        }
    }
    .select--styled {
        position: relative;
        select {
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
        }
        &:after {
            content: '';
            position: absolute;
            right: 8px;
            bottom: 35px;
            width: 15px;
            height: 15px;
            border: 2px solid blue;
            border-left: 2px solid transparent;
            border-top: 2px solid transparent;
            transform: rotate(45deg);
            pointer-events: none;
        }
    }
    input[type='radio'],
    input[type='checkbox'] {
        width: auto;
        margin-top: $scale-0
    }
    input[type="checkbox"]+label,
    input[type="radio"]+label {
        display: inline-block;
        margin-left: 0.44444rem;
        margin-right: 0.88889rem;
        margin-bottom: 0;
        vertical-align: baseline;
    }
    input[type='submit'] {
        @extend .button;
        width: auto;
        border: 0;
    }
    button,
    .button {
        -webkit-appearance: none;
        @include primary-font-semibold;
        @include smakk-borders(1px, solid, rgba(0, 0, 0, 0));
        -webkit-appearance: none;
        -moz-appearance: none;
        border-radius: $radius;
        font-size: $scale-0;
        cursor: pointer;
        margin: 0 $scale-0 $scale-2;
        min-width: 190px;
        position: relative;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        padding: 0.88889rem $scale-6 0.94444rem $scale-6;
        color: #fff;
        transition: all 300ms ease-out;
        &:hover {
            background: white;
            color: $c-blue;
            border: 1px solid $c-blue;
        }
    }
    .button--ghost {;
      border: 1px solid #fff !important;
        background: rgba(0, 0, 0, 0);
    }
    .button--callout {
        background: #fff;
        color: $c-blue  !important;
      border: 1px solid #fff !important;
        &:hover {
            background: $c-blue;
            color: white !important;
            border: 1px solid $c-blue !important;
        }
    }
    //svg icons
    .icon {
        width: 80px;
        height: 100px;
        fill: black
    }
    //Toggles
    button.toggle__item,
    .toggle__item {
        @include smakk-inline-list;
        @include smakk-toggle-item(15px, 15px);
        transition: all 200ms ease-out;
    }
    .toggle__item--large {
        @include smakk-toggle-item(20px, 20px);
    }

    //Navigation ON SMALL
    .nav-main__item {
        @include smakk-nav-links(#fff, 20px);
    }

    .main-menu__logo {
        max-width: 120px;
        margin-right: $scale-3;
        vertical-align: -webkit-baseline-middle;
    }
    ul.nav-main__submenu {
        right: 0;
        background: #0067FF;
        flex-direction: row;
        flex-wrap: wrap;
        flex-direction: row;
        flex-wrap: wrap
    }
    .nav-main__subitem__text {
        display: none;
    }
    .nav-main__subitem__text a {
        display: none;

    }
    //NAV ON LARGE
    @include smakk-breakpoint(large) {
        .nav-main__item {
            @include smakk-nav-links($c-blue, 16px);
        }
        .nav-main__subitem {
            padding: $scale-4;
            max-width: 50%;
        }
        a.nav-main__subitem__title {
            color: $c-green;
            font-size: $scale-4;
            padding: 0 !important;
            font-weight: 700
        }
        .nav-main__subitem__text {
            color: #fff;
            display: block;
            padding: 0 !important;
            margin: $scale-1 0
        }
        .nav-main__subitem__text a {
            display: inline-block;
            margin-top: $scale-0;
            @include smakk-nav-links(#fff, 16px);
            border-bottom: 2px solid $c-green;
        }
    }
    .nav-footer {
        @include smakk-inline-list;
    }
    .nav-footer__item {
        @include smakk-nav-links($c-navy, $scale-0)
    }
}
